.MainPages__welcomePoint {
  background-image: url(/static/media/WelcomePoint.6e05be0b.gif);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  min-height: 1000px;
  position: relative;
}

.MainPages__welcomePointText {
  color: #f3ede1;
  font-size: 50px;
  font-weight: 900;
  position: absolute;
  width: 100%;
  text-align: center;
  margin-top: 450px;
  text-shadow: 0px 0px 20px white;
  animation: appearTitle 3s cubic-bezier(0.215, 0.61, 0.355, 1) both, DisappearTitle 0.3s 5s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
}

@keyframes appearTitle {
  0% {
    letter-spacing: 1em;
    transform: translateZ(400px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}

@keyframes DisappearTitle {
  0% {
    transform: translateZ(0);
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    letter-spacing: -0.5em;
    transform: translateZ(-500px);
    opacity: 0;
  }
}

