@keyframes ChangeColor {
  0% {
    background: linear-gradient(to right, #ee4542, #5182ca);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  20% {
    background: linear-gradient(to right, #920be0, #e718c5);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  40% {
    background: linear-gradient(to right, #0ed186, #b8e70e);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  60% {
    background: linear-gradient(to right, #e49492, #683f79);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  80% {
    background: linear-gradient(to right, #79f029, #b10808);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  100% {
    background: linear-gradient(to right, #ee6f06, #e2ba08);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

