.body__img1 {
  position:absolute;  
  width: 10%;
  height:10%;
  left: 10%;
  top: 10%;
  border:none;
  /* transform-origin은 해당 엘리먼트에 차후
  transform이 일어났을 때 기준점을 설정해줌 */
  animation-name: paperplane;
  animation-duration: 4s;
  animation-duration: leaner;  
  animation-direction: alternate;
  animation-fill-mode: forwards;
    
}
@keyframes paperplane{
  from {
    margin-left: 0%;        
    transform-origin: right;      
    transform: 
    rotateX(0deg)
    rotateY(0deg)
    rotateZ(40deg);
    animation-timing-function: cubic-bezier(1, 1, 1, 1);
  }
  50% {
    margin-left: 30%;
    margin-top: 10%;
    transform-origin: right;      
    transform: 
    rotateX(0deg)
    rotateY(0deg)
    rotateZ(45deg);
    animation-timing-function: cubic-bezier(1, 1, 1, 1);
  }
  
  to {
    margin-left: 60%;
    transform-origin: right;      
    transform: 
    rotateX(90deg)
    rotateY(0deg)
    rotateZ(0deg);    
  }
}

.body__img2 {
  position: absolute;  
  width: 20%;
  height: 20%;
  left: 40%;
  top: 40%;
  opacity: 0;  
  animation: oldplane;
  animation-delay: 4s;
  animation-duration: 2s;
}
@keyframes oldplane {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
