.LogOuts__content {
  background: linear-gradient(to bottom, rgb(233, 222, 201), rgb(170, 170, 170));
  margin: 20% auto;
  padding: 2%;
  border: 3px solid rgb(90, 88, 89);
  width: 31%;
  border-radius: 30px;
}

.LogOuts__message {
  font-weight: 800;
  font-size: 23px;
  color: #3a3938;
  width: 70%;
  letter-spacing: 2px;
  font-family: "Single Day", cursive;
  margin-left: 4%;
}

.LogOuts__content__pic {
  border-radius: 100px;
  border: 5px solid #caaa89;
  width: 100%;
  height: 100%;
}

