.MainGlassess {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
    to right,
    rgba(20,20,20,0) 10%,
    rgba(20,20,20,0.2) 30%,
    rgba(20,20,20,0.4) 60%,
    rgba(20,20,20,0.6) 100%
  ), url("../../../TestPic/glassesBG_blur.jpg");
  background-repeat: no-repeat;
  background-size: 1835px;
  min-height: 1000px;
  position: relative;
}

.MainGlassess__text {
  color: white;
  font-size: 50px;
  font-weight: 700;
  position: absolute;
  width: 100%;
  text-align: center;
  margin-top: 150px;
}

.mainGlasses__glassesContainer {
  display: flex;
  justify-content: space-between;
}

.mainGlasses__temp_L {
  display: flex;
  justify-content: center;
  padding: 40px;
  position: absolute;
  left: 123px;
  width: 670px;
  background: url("../../../TestPic/glassesBG.jpg") no-repeat;
  background-size: 277%;  
  background-position: -128px -190px;
  border: 5px solid rgb(212, 203, 146);
  height: 670px;
  border-radius: 50%;
  top: 188px;
  transition-property: top, background-position;
  transition-duration: 1.25s;
}

